import { get } from "svelte/store";
import WidthHandle from "./WidthHandle.svelte";
import ScreenOption from "./ScreenOption.svelte";
import AcHtmlElement from "AC/helpers/html-element";
import { persistColumnWidthsPreference, persistColumnWidthsSetting, persistUserPreference, restoreUserPreferences, restoreUserPreferencesColumn } from "./ajax";
import { clearColumnWidthStyleBlock, getModuleLocalizedVariables } from "./helpers";
import { alteredWidths } from "./store";
export default class WidthConfigurator {
    constructor(table, active) {
        this.active = active;
        this.handles = {};
        this.table = table;
        this.setHeaderCells();
        this.setScreenOption();
        this.alteredCells = alteredWidths;
        if (active) {
            this.enable();
        }
    }
    setScreenOption() {
        let container = document.querySelector('#acp-screen-option-tools');
        this.screenOption = new ScreenOption({
            target: container,
            props: {
                active: this.active,
                onActivate: () => this.enable(),
                onDeactivate: () => this.disable(),
                onStoreDefault: () => this.storeListScreenSetting(),
                onReset: () => {
                    this.reset();
                    this.restoreUserPreferences().then(() => {
                        window.location.reload();
                    });
                }
            }
        });
    }
    resetCell(cell) {
        if (this.handles.hasOwnProperty(cell.id)) {
            cell.querySelectorAll('.acp-cr-container').forEach((el) => el.remove());
            delete (this.handles[cell.id]);
            this.initCell(cell);
        }
    }
    reset() {
        this.headerCells.forEach(cell => this.resetCell(cell));
        this.initCells();
    }
    enable() {
        this.initCells();
        this.storeActivePreference(true);
    }
    disable() {
        this.storeActivePreference(false);
    }
    getAlteredCellWidths() {
        return get(this.alteredCells);
    }
    restoreUserPreferences() {
        return restoreUserPreferences();
    }
    storeListScreenSetting() {
        persistColumnWidthsSetting(this.getAlteredCellWidths());
    }
    storeUserPreference(e) {
        persistColumnWidthsPreference(e.id, parseInt(e.dataset.alteredWidth));
    }
    storeActivePreference(active) {
        persistUserPreference(active);
    }
    handleSingleColumnReset(cell) {
        this.resetCell(cell);
        clearColumnWidthStyleBlock(cell.id, 'user');
        restoreUserPreferencesColumn(cell.id);
    }
    setHeaderCells() {
        this.headerCells = [];
        this.table.Columns.getColumnNames().forEach(name => {
            let cell = this.table.getElement().querySelector(`thead [id="${name}"]`);
            if (cell) {
                this.headerCells.push(cell);
            }
        });
    }
    initCell(cell) {
        this.handles[cell.id] = new WidthHandle({
            target: AcHtmlElement.create('div').addClass('acp-cr-container').appendSelfTo(cell).element,
            props: {
                minWidth: getModuleLocalizedVariables().minimal_pixel_width,
                columnName: cell.id,
                tableCell: cell,
                table: this.table.getElement(),
                onChange: () => this.storeUserPreference(cell),
                onReset: () => this.handleSingleColumnReset(cell)
            }
        });
    }
    initCells() {
        this.headerCells.forEach(cell => {
            if (!this.handles.hasOwnProperty(cell.id)) {
                this.initCell(cell);
            }
        });
    }
}
