export const clearColumnWidthStyleBlock = (column, type) => {
    let element = document.querySelector(getColumnWidthStyleSelector(column, type));
    if (element) {
        element.remove();
    }
};
export const getColumnWidthStyleSelector = (column, type) => `#ac-column-size-${type}-${column}`;
export const getModuleLocalizedVariables = () => ACP_COLUMN_RESIZE;
export const getModuleTranslation = () => ACP_COLUMN_RESIZEi18n;
