<script lang="ts">
    import {LocalizedAcTable} from "AC/types/table";
    import {alteredWidths, canReset} from "./store";

    export let width: string;
    export let columnName: string;
    export let table: HTMLElement;
    export let tableCell: HTMLElement;
    export let onChange: Function;
    export let onReset: Function;
    export let minWidth: number = 40;

    let element: HTMLElement;
    let styleElement: HTMLElement;
    let clickOffset: number;
    let isDragging: boolean = false;
    let showReset: boolean = false;
    let changedWidth: number;
    let finalWidth: string;

    declare const AC: LocalizedAcTable;

    const handleResetColumn = () => {
        alteredWidths.remove( columnName );
        if (typeof onReset !== 'undefined') {
            onReset();
        }
    }

    const onFinish = () => {
        window.removeEventListener('mousemove', mouseMove);
        isDragging = false;
        table.classList.remove('acp-width-conf-dragging');
        tableCell.style.zIndex = '1';
        canReset.yes();

        if (showReset) {
            handleResetColumn();
            showReset = false;
            return;
        } else {
            finalWidth = element.style.width;

            alteredWidths.changeWidth(columnName, changedWidth);
            calculateStyle();
            setTimeout(() => {
                if (onChange) {
                    onChange();
                }
            });
        }
    }

    const calculateStyle = () => {
        // Use inline style in order to use media queries
        let newStyle = `@media screen and (min-width: 784px){
        		body.ac-${AC.list_screen} .wrap table th.column-${columnName} { width: ${finalWidth} !important; }
				body.acp-overflow-table.ac-${AC.list_screen} .wrap th.column-${columnName} { min-width: ${finalWidth}!important; }
        	}`;

        styleElement.innerText = newStyle.trim();
        // Reset the element again to make sure it works on unsupported widths as well
        element.style.width = '100%';
    }

    const mouseDown = (e) => {
        tableCell.style.zIndex = '1000';
        isDragging = true;

        clickOffset = e.pageX - e.target.getBoundingClientRect().left;
        table.classList.add('acp-width-conf-dragging');
        window.addEventListener('mousemove', mouseMove);
        window.addEventListener('mouseup', () => onFinish(), {once: true});
    }

    const mouseMove = (e) => {
        e.preventDefault();
        e.stopPropagation();

        if (!isDragging) {
            return;
        }

        let _width = Math.round((e.pageX + clickOffset) - (element.getBoundingClientRect().left));

        showReset = _width < 0

        if (_width < minWidth) {
            _width = minWidth;
        }

        width = _width.toString() + 'px';
        element.style.width = width;
        changedWidth = _width;
        tableCell.dataset.alteredWidth = _width.toString();
    }

</script>
{#if showReset}
	<span class="acp-width-conf__reset">Reset</span>
{/if}
<div class="acp-width-conf-dragger" class:active={isDragging} class:-reset={showReset} bind:this={element}>

	{#if isDragging && width && !showReset}
		<span class="acp-width-conf__width">{width}</span>
	{/if}
	<span class="acp-width-conf-handle" on:mousedown={mouseDown}>
	</span>
	{#if width}
		<style bind:this={styleElement}>
		</style>
	{/if}
</div>