import { writable } from 'svelte/store';
import { getModuleLocalizedVariables } from "./helpers";
function createCanReset(initial) {
    const { subscribe, update } = writable(initial);
    return {
        subscribe,
        yes: () => update(() => true),
        no: () => update(() => false),
    };
}
function createCanSetDefault(initial) {
    const { subscribe, update } = writable(initial);
    return {
        subscribe,
        yes: () => update(() => true),
        no: () => update(() => false),
    };
}
function createAlteredWidths(initial) {
    const { subscribe, update } = writable(initial);
    return {
        subscribe,
        changeWidth: (column_name, width) => {
            let newValue = {};
            newValue[column_name] = width;
            update((n) => {
                return Object.assign({}, n, newValue);
            });
        },
        remove: (column_name) => {
            update((n) => {
                delete (n[column_name]);
                return n;
            });
        },
        get: () => {
            return initial;
        }
    };
}
export const alteredWidths = createAlteredWidths({});
export const canReset = createCanReset(getModuleLocalizedVariables().can_reset);
export const canSetDefault = createCanSetDefault(getModuleLocalizedVariables().can_set_default);
