<script lang="ts">
    import Toggle from "../component/Toggle.svelte";
    import {createEventDispatcher} from 'svelte';
    import {canReset, canSetDefault} from "./store";
    import {getModuleTranslation} from "./helpers";

    const dispatch = createEventDispatcher();

    export let onReset: Function;
    export let onStoreDefault: Function;
    export let onActivate: Function;
    export let onDeactivate: Function;
    export let active: boolean;

    const handleOnToggle = (e) => {
        if (e.detail.checked) {
            onActivate();
        } else {
            onDeactivate();
        }
    };

    const handleReset = () => {
        typeof onReset !== 'undefined' ? onReset() : '';
    }

    const handleStoreDefault = () => {
        typeof onStoreDefault !== 'undefined' ? onStoreDefault() : '';
    }

    const i18n = getModuleTranslation();
</script>

<div class="acp-so-tool-container acp-so-tool">
	<div class="acp-so-tool__option" data-ac-tip={i18n.tip_resize}>
		<Toggle label={i18n.resize_columns_tool} checked={active} on:change={ handleOnToggle } name="acp-toggle-column-resize"/>
	</div>
	<div class="acp-so-tool__actions">
		{#if $canReset}
			<a href="" on:click|preventDefault={handleReset} data-ac-tip={i18n.tip_reset}>{i18n.reset}</a>
		{/if}
		{#if $canSetDefault}
			<a href="" on:click|preventDefault={handleStoreDefault} data-ac-tip={i18n.tip_set_default}>{i18n.set_default}</a>
		{/if}
	</div>
</div>