<script lang="ts">
    import {createEventDispatcher} from 'svelte';

    export let label;
    export let name;
    export let value = 1;
    export let checked: boolean = false;

    const dispatch = createEventDispatcher();

    const onChange = () => {
        dispatch('change', {
            'checked': checked
        });
    }

</script>

<div class="ac-toggle-v2">
	<span class="ac-toggle-v2__toggle">
		<input class="ac-toggle-v2__toggle__input" name={name} id={name} type="checkbox" value={value} bind:checked={checked} on:change={onChange}>
		<span class="ac-toggle-v2__toggle__track"></span>
		<span class="ac-toggle-v2__toggle__thumb"></span>
	</span>
	<label for={name} class="ac-toggle-v2__label">{ label }</label>
</div>