import axios from "axios";
import { mapDataToFormData } from "AC/helpers/global";
export const persistColumnWidthsSetting = (column_widths) => {
    return axios.post(ajaxurl, mapDataToFormData({
        'action': 'acp-list-column-width',
        'column_widths': column_widths,
        'list_id': AC.layout,
        '_ajax_nonce': AC.ajax_nonce
    }));
};
export const persistColumnWidthsPreference = (column_name, width) => {
    return axios.post(ajaxurl, mapDataToFormData({
        'action': 'acp-user-column-width',
        'list_id': AC.layout,
        'column_name': column_name,
        'width': width,
        '_ajax_nonce': AC.ajax_nonce
    }));
};
export const persistUserPreference = (active) => {
    return axios.post(ajaxurl, mapDataToFormData({
        'action': 'acp-screen-tool-column-resizer',
        'active': active,
        '_ajax_nonce': AC.ajax_nonce
    }));
};
export const restoreUserPreferencesColumn = (column_name) => {
    return axios.post(ajaxurl, mapDataToFormData({
        'action': 'acp-user-column-width-reset',
        'list_id': AC.layout,
        'column_name': column_name,
        '_ajax_nonce': AC.ajax_nonce
    }));
};
export const restoreUserPreferences = () => {
    return axios.post(ajaxurl, mapDataToFormData({
        'action': 'acp-user-column-width-reset-all',
        'list_id': AC.layout,
        '_ajax_nonce': AC.ajax_nonce
    }));
};
